import React from "react"
import "./FormAuth.css"
import Loader from "../images/Ripple-1.1s-276px.svg"
// import "./Loader.css"


const FormAuth=({

    email,
    setEmail,
    password,
    setPassword,
    handleLogin,
    handleSignUp,
    hasAccount,
    setHasAccount,
    emailError,
    passwordError,
    authAccount,
    isLoader
})=>{

    return(
        <>

        {isLoader ? 
            <section className="login">
            <form className="loginContainer">
            <img src={Loader} alt="loader" className="loader-svg"/>
            <h2 className="text-white text-center mt-5">Loading...</h2>
            </form>
            </section>
         :
        <section className="login">
            <form className="loginContainer">
                <label>Username</label>
                <input 
                type="email" 
                autofocus 
                required 
                value={email}
                onChange={(e)=> setEmail(e.target.value)}
                />
                <p className="errorMsg">{emailError}</p>
                <label>Password</label>
                <input

                type="password"
                required
                value={password}
                onChange={(e)=> setPassword(e.target.value)}
                />
                <p className="errorMsg">{passwordError}</p>
                <div className="errorMsg">
                    {hasAccount ? 
                    (
                        <>
                        <button className="account" onClick={handleLogin}>Iniciar sesi貌n</button>
                        <p className="pForm">No tienes una cuenta ? <span onClick={()=> setHasAccount(!hasAccount)}>Sign up</span></p>
                        </>
                    
                    ) 
                    : 
                    (
                        <>
                        <button className="account" onClick={handleSignUp}>Registrate</button>
                        <p className="pForm">Tienes una cuenta ? <span onClick={()=> setHasAccount(!hasAccount)} >Sign in</span></p>
                        </>
                    )}
                </div>
            </form>
        </section>
        }
        
        </>
    )
}


export default FormAuth;