import React from "react";
import "./Modal.css";
import CloseSvg from "../images/close.svg"

const Modal =({showModal, setShowModal, showModalImg})=>{

    const closeModal=()=>{


        setShowModal(prev => !prev)
    }

    return(

        <>
            {showModal ? 
                <div className="backGroundModal" onClick={closeModal}>

                    <div className="modalWrapper">
                    <img src={CloseSvg} alt="close" className="closeSvg"/>
                    <img src={showModalImg} alt={showModalImg} className="modalImg"/>
                    </div>
                    
                </div>
                : null
            }
        </>
    )
}

export default Modal;