import React, { useState } from "react";
import Cocinar from "../images/cocinar.svg";
import Close from "../images/close.svg";
import NavBar from "../images/menu (1).svg";
import {Link} from "react-router-dom";
import "../styles/bootstrap.css"
import "./Navbar.css"
import "../App.css"

const Navbar=({authAccount, handleLogout})=>{

    const [isOpen, setIsOpen] = useState(false);
    
    const openMenu=()=> setIsOpen(!isOpen)
    const isCloseMenu =()=>setIsOpen(false)

    let classNameList="NavBar-List-my"

    if(!isOpen){

        classNameList += " NavBar-List-my-display-none";
    }

    

    return(

        <nav className="navbar navbar-expand-lg  navbar-my border-bottom-my">
            <div className="container">
            
                    <div className="col">
                        <button className="button-navbar-my" onClick={openMenu}>
                            <img  src={NavBar} alt="NavBar" className="img-fluid-my"/>
                        </button>
                    </div>

                    <div className="col">
                        

                        <Link to="/" style={{textDecoration:"none"}}>
                        <div className="text-center text-tuPastelIDeal font-principal">
                            <img  src={Cocinar} alt="cocinar" className="img-fluid-my"/>
                        </div>
                        </Link>
                    </div>
                    <div className="col-4 ">
                        

                        

                        {authAccount ? <button className="btn btn-danger float-end" onClick={handleLogout}>Logout</button> :
                         <Link to="/account" className="float-end">
                            <button className="btn btn-success">Account</button>
                        </Link>}

                        
                    </div>

                    <ul className={classNameList} >

                        <div className="container-img-close"  onClick={isCloseMenu} style={{cursor:"pointer"}}>
                            <img  src={Close} alt="close" className="img-fluid-my"/>
                        </div>
                        {/* <li className="nav-item">
                            <Link to="/sobreMi" onClick={isCloseMenu} className="nav-link">Sobre M铆</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/recetas" onClick={isCloseMenu} className="nav-link">Recetas</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/ubicame" onClick={isCloseMenu} className="nav-link">Donde Ubicarme</Link>
                        </li> */}
                        <li className="nav-item">
                            <Link to="/mistrabajos" onClick={isCloseMenu} className="nav-link">Mis Trabajos</Link>
                        </li>
                    </ul>
            </div>
        </nav>

    )
}


export default Navbar;
