import React, {useEffect, useState} from "react";
import Loader from "../images/Ripple-1.1s-276px.svg";
import Search from "../images/search-1.svg"
import "../App.css"
import "../styles/bootstrap.css"
import "./ImageGrid.css"
import "./FormAuth.css"
import { getImages } from "../api";
import Modal from "./Modal";
import "./Who.css"
import GalleryJson from "../assets/getDataBase.json"


const ImageGrid =()=>{

    const [images, setImages] = useState([]);
    const [nextCursor, setNextCursor] = useState(null);
    // const [isLoader, setIsloader] = useState(true);
    const [showModal, setShowModal] = useState(false);
    const [showModalImg, setShowModalImg] = useState(null);


    const handleLoadMoreButtonClick = async ()=> {

        const responseJson = await getImages(nextCursor)
        setImages((currentImagesList) => [...currentImagesList, ...responseJson.resources,])

        setNextCursor(responseJson.next_cursor)
    }


    const openModal=(e)=>{

        setShowModal(prev => !prev)
        
        setShowModalImg(e.target.offsetParent.childNodes[0].alt)
        console.log(e.target.offsetParent.childNodes[0].alt)
        
    }

    useEffect(()=>{

        // const fetchData = async ()=>{

        //     const responseJson = await getImages();
            
        //     setImages(responseJson.resources)
        //     setNextCursor(responseJson.next_cursor)
        //     setIsloader(false)
        //     console.log(images)
        // }

        // fetchData()

        

        let docImg = []

        GalleryJson.resources.forEach((el)=>{

            docImg.push(el)
        })

        setImages(docImg)

        console.log(images)

    }, [])


    return(
        <section className="image-grides">
        <Modal showModal={showModal} setShowModal={setShowModal} showModalImg={showModalImg}>
            {"hola"}
        </Modal>

        <h2 className="font-principal my-font-size text-center my-padding-top mb-5 neonText text-white">Mis Trabajos</h2>
        <div className="container">
        <div class="image-grid-my" id="countryForRegion">

            {images && images.map((el, index)=>{

            return(

                <figure className="figure" >
                <img src={el.secure_url} alt={el.secure_url} key={index} className="imagess"/>
                <div className="capa" onClick={(e)=> openModal(e)}>
                    <img src={Search} alt="search" />
                </div>
                </figure>
            )
            })}

           

            
        </div>
        </div>
        {nextCursor && <div className="text-center mt-5 mb-5"><button className="btn btn-success" onClick={handleLoadMoreButtonClick}>Load More</button></div>}

        </section>
    )
}


export default ImageGrid;





